/** @jsx jsx */
import { jsx } from '@emotion/core';
import {
  Grid,
  FormGroup,
  FormControlLabel,
  Checkbox,
  TextField,
  Radio,
  Button,
  FormHelperText,
  RadioGroup,
} from '@material-ui/core';
import { useCallback, useMemo } from 'react';
import { useForm } from 'react-hook-form';
import Section from 'components/Forms/MentoringApply/Section';
import Fieldset from 'components/Forms/MentoringApply/Fieldset';
import { MentoringApplyFormDto } from 'models/forms';
import { jobs, positions, careers, applyReasons, workingTimes } from 'src/constants/applyForm';

interface Props {
  onSubmit?: (data: MentoringApplyFormDto) => void;
}
const MentoringApplyForm = ({ onSubmit }: Props) => {
  const { register, handleSubmit, errors, watch } = useForm();
  const formValues = watch();

  const hasPosition = useMemo(() => {
    return formValues.positions && formValues.positions.length > 0;
  }, [formValues.positions]);

  const onSubmitHandler = useCallback(
    data => {
      const formData = { ...data };
      if (formData.job === '') {
        formData.job = formData.otherJob;
        delete formData.otherJob;
      }
      if (formData.applyReasons.includes('')) {
        formData.applyReasons.push(formData.otherReason);
        delete formData.otherReason;
      }

      onSubmit?.(formData);
    },
    [onSubmit]
  );
  console.log(errors);
  return (
    <Grid component="form" container noValidate autoComplete="off" onSubmit={e => e.preventDefault()}>
      <Section title="   .">
        <Fieldset label="" required={true}>
          <TextField
            name="email"
            type="email"
            placeholder="lubycon@gmail.com"
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: ' ',
              },
              pattern: {
                value: /\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/,
                message: '  ',
              },
            })}
            error={Boolean(errors.email)}
            helperText={errors.email?.message ?? ''}
          />
        </Fieldset>
        <Fieldset label="" required={true}>
          <TextField
            name="name"
            placeholder=""
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: ' ',
              },
            })}
            error={Boolean(errors.name)}
            helperText={errors.name?.message ?? ''}
          />
        </Fieldset>
        <Fieldset label="  (- )" required={true}>
          <TextField
            name="phoneNumber"
            type="tel"
            placeholder="01012341234"
            variant="outlined"
            inputRef={register({
              required: {
                value: true,
                message: '  ',
              },
              pattern: {
                value: /^01\d\d{4}\d{4}$/,
                message: '   ',
              },
            })}
            error={Boolean(errors.phoneNumber)}
            helperText={errors.phoneNumber?.message}
          />
        </Fieldset>
      </Section>
      <Section title="    ?">
        <Fieldset label="" required={true} error={Boolean(errors.job)}>
          <RadioGroup row defaultValue={jobs[0]}>
            {jobs.map(job => (
              <FormControlLabel
                key={job}
                name="job"
                label={job}
                value={job}
                control={<Radio />}
                inputRef={register({
                  required: {
                    value: true,
                    message: ' ',
                  },
                })}
              />
            ))}
          </RadioGroup>
          {formValues.job === '' && (
            <TextField
              name="otherJob"
              placeholder=" "
              variant="outlined"
              inputRef={register({
                required: {
                  value: true,
                  message: ' ',
                },
              })}
              error={Boolean(errors.otherJob)}
              helperText={errors.otherJob?.message ?? ''}
            />
          )}
          {errors.job?.message && <FormHelperText>{errors.job?.message ?? ''}</FormHelperText>}
        </Fieldset>
        <Fieldset label=" ">
          <FormGroup row>
            {positions.map(position => (
              <FormControlLabel
                name="positions"
                key={position}
                label={position}
                value={position}
                inputRef={register()}
                control={<Checkbox />}
              />
            ))}
          </FormGroup>
        </Fieldset>
        {hasPosition && (
          <Fieldset label="    ?">
            <RadioGroup row defaultValue={careers[0]}>
              {careers.map(carrer => (
                <FormControlLabel
                  key={carrer}
                  name="career"
                  label={carrer}
                  value={carrer}
                  inputRef={register({
                    required: {
                      value: true,
                      message: ' !',
                    },
                  })}
                  control={<Radio />}
                />
              ))}
            </RadioGroup>
            {errors.career && <FormHelperText>{errors.career?.message ?? ''}</FormHelperText>}
          </Fieldset>
        )}
      </Section>
      <Section title=" ">
        <Fieldset label="    ? (   )">
          <FormGroup>
            {applyReasons.map(reason => (
              <FormControlLabel
                name="applyReasons"
                key={reason}
                label={reason}
                value={reason}
                inputRef={register()}
                control={<Checkbox />}
              />
            ))}
          </FormGroup>
          {formValues.applyReasons && formValues.applyReasons.includes('') && (
            <TextField name="otherReason" placeholder=" " variant="outlined" inputRef={register()} />
          )}
        </Fieldset>
        <Fieldset label="      ?  ,    ?">
          <TextField name="projectItem" multiline={true} variant="outlined" inputRef={register()} />
        </Fieldset>
        <Fieldset
          label="    1      ?"
          required={true}
          error={Boolean(errors.workingTime)}
        >
          <RadioGroup row defaultValue={workingTimes[0]}>
            {workingTimes.map(time => (
              <FormControlLabel
                key={time}
                name="workingTime"
                label={time}
                value={time}
                control={<Radio />}
                inputRef={register({
                  required: {
                    value: true,
                    message: ' ',
                  },
                })}
              />
            ))}
          </RadioGroup>
          {errors.workingTime?.message && <FormHelperText>{errors.workingTime?.message ?? ''}</FormHelperText>}
        </Fieldset>
        <Fieldset label="       !">
          <TextField name="expectThing" multiline={true} variant="outlined" inputRef={register()} />
        </Fieldset>
      </Section>
      <Section>
        <Fieldset
          label="  ,         
                  !"
        >
          <FormControlLabel
            name="isAgreeToCollectTalentPool"
            label=""
            inputRef={register()}
            control={<Checkbox />}
          />
        </Fieldset>
        <Button
          type="submit"
          color="primary"
          size="large"
          variant="contained"
          fullWidth={true}
          onClick={handleSubmit(onSubmitHandler)}
        >
          
        </Button>
      </Section>
    </Grid>
  );
};

export default MentoringApplyForm;
