/** @jsx jsx */
import { jsx } from '@emotion/core';
import Page from 'src/components/Layout/Page';
import {
  Container,
  Grid,
  Typography,
  Paper,
  makeStyles,
  Theme,
  createStyles,
  Dialog,
  DialogContent,
  DialogActions,
  Button,
} from '@material-ui/core';
import Emoji from 'components/Utils/Emoji';
import MentoringApplyForm from 'components/Forms/MentoringApply';
import { useCallback, useState } from 'react';
import { MentoringApplyFormDto } from 'src/models/forms';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    paper: {
      padding: theme.spacing(2),
      [theme.breakpoints.up('md')]: {
        padding: theme.spacing(4),
      },
    },
  })
);

const MentoringApply = () => {
  const styles = useStyles();

  const [isOpenDialog, setDialogOpenState] = useState(false);
  const [testData, setTestData] = useState('');
  const handleSubmit = useCallback((data: MentoringApplyFormDto) => {
    setTestData(JSON.stringify(data, null, 2));
    setDialogOpenState(true);
  }, []);

  return (
    <Page
      pageName="   "
      showTitle={true}
      description={
        <Typography component="span">
               ! <Emoji emoji="" name="Hello" />
        </Typography>
      }
    >
      <Dialog open={isOpenDialog} onClose={() => setDialogOpenState(false)}>
        <DialogContent>
          <pre>{testData}</pre>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDialogOpenState(false)}></Button>
        </DialogActions>
      </Dialog>
      <Container>
        <Grid container justify="center">
          <Grid item xs={12} md={8}>
            <Paper className={styles.paper} elevation={3}>
              <MentoringApplyForm onSubmit={handleSubmit} />
            </Paper>
          </Grid>
        </Grid>
      </Container>
    </Page>
  );
};

export default MentoringApply;
